@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
:root {
    --bio-text-shadow: red;
    --description-a: rgb(47, 34, 228);
    --description-svg: black;
    --description-svg-hover: #f02626;
    --bg: white;
    --text: black;
    --mode-toggle: rgb(0, 0, 0);
    --profile-shadow: rgba(100, 100, 111, 0.3);
    --post-border: black;
    --post-border-hover: rgb(231, 43, 43);
    --menu: rgb(240, 240, 240);
    --time-color: rgb(100, 100, 100);
    --description-color: rgb(50, 50, 50);
}

[theme="dark"] {
    --bio-text-shadow: red;
    --description-a: #62daff;
    --description-svg: white;
    --description-svg-hover: #1c9ec5;
    --bg: #0b0411;
    --text: white;
    --mode-toggle: rgb(236, 236, 236);
    --profile-shadow: rgba(200, 200, 200, 0.2);
    --post-border: white;
    --post-border-hover: rgb(221, 74, 74);
    --menu: #190c25;
    --menu-item-hover: #261338;
    --time-color: rgb(150, 150, 150);
    --description-color: rgb(200, 200, 200);
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Ubuntu", sans-serif;
    color: black;
    color: var(--text);
}

html,
body {
    width: 100%;
    height: 100%;
}

body {
    background-color: white;
    background-color: var(--bg);
    padding: 40px 0;
}

.main {
    width: 100%;
    display: flex;
    justify-content: center;
}

.mode-toggle {
    fill: rgb(0, 0, 0);
    fill: var(--mode-toggle);
    position: fixed;
    top: 20px;
    right: 20px;
    stroke-width: 0;
}

.back {
    fill: rgb(0, 0, 0);
    fill: var(--mode-toggle);
    position: fixed;
    top: 20px;
    left: 20px;
    stroke-width: 0;
}

.back>svg {
    width: 28px;
    height: 28px;
}
.home {
    padding-top: 10vh;
    width: 80%;
    display: flex;
    justify-content: space-between;
}
.profile {
    width: 40%;
}

.profile>.about>img {
    border-radius: 5px;
    width: 200px;
    height: 200px;
    margin-bottom: 20px;
    box-shadow: 0px 7px 29px 0px var(--profile-shadow);
}

.about>h1 {
    font-weight: bolder;
    margin-bottom: 20px;
}

.about>h1>span {
    font-size: 1.1em;
    transition: 0.5s;
}

.about>h1>span:hover {
    text-shadow: 2px 2px var(--bio-text-shadow);
    transition: 0.5s;
}

.description {
    margin-bottom: 15px;
}

.description>p>a {
    text-decoration: none;
    color: var(--description-a);
}

.description>p>a:hover {
    border-bottom: 2px solid var(--description-a);
    transition: 0.5s;
}

.social-media>a>svg {
    fill: var(--description-svg);
    width: 40px;
    cursor: pointer;
    transition: 0.5s;
}

.social-media>a>svg:hover {
    fill: var(--description-svg-hover);
    transition: 0.5s;
}
.right {
    width: 55%;
}

.menu {
    display: flex;
    width: 100%;
    background-color: var(--menu);
    border-radius: 5px;
    margin-bottom: 20px;
}
.menu>div {
    cursor: pointer;
    padding: 10px 15px;
    transition: 0.5s;
    position: relative;
}

.menu>div:first-of-type {
    border-radius: 5px 0 0 5px;
}


/* 
.menu>div:last-of-type {
    border-radius: 0 5px 5px 0;
} */

.menu>div:hover {
    background-color: var(--menu-item-hover);
    transition: 0.5s;
}

.menu>div.active {
    background-color: var(--menu-item-hover);
}

.menu>div.active>div {
    width: 10px;
    height: 10px;
    background-color: var(--menu-item-hover);
    position: absolute;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: calc(100% - 5px);
    left: calc(50% - 5px);
}
.posts>h1 {
    font-size: 20px;
    margin-bottom: 20px;
}
.post {
    display: block;
    border-radius: 5px;
    border: 1px solid var(--post-border);
    padding: 15px 20px;
    margin-bottom: 20px;
    text-decoration: none;
    transition: 0.5s;
}

.post:hover {
    border: 1px solid var(--post-border-hover);
    transition: 0.5s;
}

.post>.header {
    display: flex;
    align-items: center;
    height: 100%;
    margin-bottom: 5px;
}

.header>h6:first-of-type {
    margin-right: 10px;
}

.post>h1 {
    margin-bottom: 6px;
}
.projects>h1 {
    font-size: 20px;
    margin-bottom: 20px;
}
.project {
    display: flex;
    border-radius: 5px;
    border: 1px solid var(--post-border);
    padding: 15px 20px;
    margin-bottom: 20px;
    text-decoration: none;
    transition: 0.5s;
}

.project:hover {
    border: 1px solid var(--post-border-hover);
    transition: 0.5s;
}

.project>.header {
    width: 100px;
    height: 100px;
    border-radius: 5px;
    margin-right: 20px;
}

.project>.header>img {
    width: 100px;
    height: 100px;
    border-radius: 5px;
}

.project>.content {
    display: flex;
    flex-direction: column;
}

.project>.content>h1 {
    margin-bottom: 10px;
}

.project>.content>p {
    margin-bottom: 5px;
}

.link {
    display: flex;
}

.link>svg {
    width: 20px;
    height: 20px;
    fill: var(--text);
    margin-right: 5px;
}

.link>a {
    text-decoration: none;
    color: var(--description-a);
}

.link>a:hover {
    border-bottom: 2px solid var(--description-a);
    transition: 0.5s;
}
.post-view {
    padding-top: 50px;
    width: 55%;
}

.post-view>.header>.time {
    display: flex;
}

.header>.time>h6 {
    margin-right: 5px;
    font-size: 13px;
    margin-bottom: 10px;
    color: var(--time-color);
}

.header>.title>h1 {
    font-weight: 900;
    font-size: 40px;
    margin-bottom: 10px;
}

.header>.title>p {
    font-size: 20px;
    font-weight: 100;
    color: var(--description-color);
    margin-bottom: 25px;
}

.post-view>.content {
    font-size: 20px;
}
